import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { JobAssignmentRoutes } from './job-assignment.routing';
import { JobAssignmentComponent } from './job-assignment.component';
import { MaterialModule } from '../app.module';
import { AgmCoreModule } from '@agm/core';
import { NguiAutoCompleteModule } from '@ngui/auto-complete';
import { Ng2AutoCompleteModule } from 'ng2-auto-complete';
import { FieldErrorDisplayModule } from '../field-error-display/field-error-display.module';
import { FileSelectDirective, FileDropDirective, FileUploadModule } from 'ng2-file-upload';

@NgModule({
    imports: [
        CommonModule,
        RouterModule.forChild(JobAssignmentRoutes),
        FormsModule,
        ReactiveFormsModule,
        MaterialModule,   
        NguiAutoCompleteModule,  
        Ng2AutoCompleteModule,  
        FieldErrorDisplayModule, 
        FileUploadModule,
        AgmCoreModule.forRoot({
            apiKey: 'AIzaSyALhBWvI6EuL2Vul0ykYis51D_ckKtxVCc',
            libraries: ["places"]
        })      
    ],
    declarations: [
        JobAssignmentComponent
    ]
})

export class JobAssignmentModule { }


